import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, Tabs, Tab, IconButton, Drawer, List, ListItem, ListItemText, useMediaQuery, useTheme } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { Link, useLocation } from 'react-router-dom';

const Navigation = () => {
   const [drawerOpen, setDrawerOpen] = useState(false);
   const theme = useTheme();
   const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
   const location = useLocation();

   const toggleDrawer = (open) => (event) => {
     if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
       return;
     }
     setDrawerOpen(open);
   };

   const menuItems = [
     { label: 'Home', path: '/' },
     { label: 'Active Business Search', path: '/active-business-search' },
     { label: 'Business Profit Calculator', path: '/business-profit-calculator' },
     { label: 'Copyright Note', path: '/copyright-note' }
   ];

   const currentTab = menuItems.findIndex(item => item.path === location.pathname);

   return (
     <AppBar position="static">
       <Toolbar>
         <Typography variant="h6" sx={{ flexGrow: 1 }}>
           LiquorBizConnect
         </Typography>
         {isMobile ? (
           <>
             <IconButton
               edge="start"
               color="inherit"
               aria-label="menu"
               onClick={toggleDrawer(true)}
             >
               <MenuIcon />
             </IconButton>
             <Drawer anchor="right" open={drawerOpen} onClose={toggleDrawer(false)}>
               <List>
                 {menuItems.map((item) => (
                   <ListItem 
                     button 
                     key={item.label} 
                     component={Link} 
                     to={item.path}
                     onClick={toggleDrawer(false)}
                   >
                     <ListItemText primary={item.label} />
                   </ListItem>
                 ))}
               </List>
             </Drawer>
           </>
         ) : (
           <Tabs 
             value={currentTab} 
             variant="standard"
             scrollButtons="auto"
             sx={{
               "& .Mui-selected": {
                 color: "white", // Set the text color for the selected tab
                 backgroundColor: "primary.main", // Set the background color for the selected tab
                 fontWeight: "bold", // Optional: make selected tab text bold
               },
               "& .MuiTabs-indicator": {
                 backgroundColor: "secondary.main", // Set the color of the indicator line
               }
             }}
           >
             {menuItems.map((item) => (
               <Tab 
                 key={item.label} 
                 label={item.label} 
                 component={Link} 
                 to={item.path} 
               />
             ))}
           </Tabs>
         )}
       </Toolbar>
     </AppBar>
   ); 
};

export default Navigation;
