import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import LiquorStoresPage from './components/LiquorStores/LiquorStoresPage';
import CopyrightNote from './components/Copyright/CopyrightNote';
import HomePage from './components/Home/HomePage';
import BusinessProfitCalculator from './components/BusinessProfitCalc/BusinesProfitCalculator';
import Navigation from './components/Navigation/Navigation';

function App() {
  return (
    <Router>
      <div className="App">
        <Navigation />
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/active-business-search" element={<LiquorStoresPage />} />
          <Route path="/business-profit-calculator" element={<BusinessProfitCalculator />} />
          <Route path="/copyright-note" element={<CopyrightNote />} />
          {/* Redirect /LiquorBizConnect to home page */}
          <Route path="/LiquorBizConnect" element={<Navigate to="/" replace />} />
          {/* Catch-all route for any undefined paths */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </div>
    </Router>    
  );
}

export default App;
